/* FIRST SCREEN ================================================================*/

.main .firstScreen {
  position: relative;
  overflow: hidden;
}

/* Swiper  */

.firstScreen .swiper-container-pointer-events {
  touch-action: pan-y;
}

.firstScreen .swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: visible;
  list-style: none;
  z-index: 1;
}

.firstScreen .swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.firstScreen .swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.firstScreen .swiper-slide {
  text-align: center;
  display: flex;
  /* background: #121212; */
}

.firstScreen .swiper-pagination {
  bottom: 0;
  left: 50%;
  z-index: 10;
  transform: translate(-50%, -50%);
  position: absolute;
  display: flex;
}

.firstScreen .swiper-pagination-bullet {
  display: block;
  border-radius: 100%;
  cursor: pointer;
  margin-right: 8px;
  width: 20px;
  height: 20px;
  background: #2b2b2b;
}

.firstScreen .swiper-pagination-bullet:last-child {
  margin-right: 0;
}

.firstScreen .swiper-pagination-bullet-active {
  background: #787878;
}

.main .benefits {
  padding: 80px 0;
}

.main .buy-crypto {
  padding: 64px 0 80px;
}

.main .buy-crypto .roadmap {
  display: flex;
  width: 82%;
  justify-content: space-between;
}

.main .buy-crypto .roadmap-img {
  position: relative;
  background: #e7eaee;
  width: 85px;
  height: 85px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main .buy-crypto .roadmap-img img {
  width: 63px;
  height: 63px;
  position: relative;
  transition: all 0.3s linear;
}

.main .buy-crypto .roadmap-img:nth-child(2) img {
  animation: rotate 2s infinite linear;
}

.main .buy-crypto .roadmap-img::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  height: 1px;
  background: #e7eaee;
}

.main .buy-crypto .roadmap-img::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(50%, -50%);
  height: 1px;
  background: #e7eaee;
}

.main .buy-crypto .roadmap-img:first-child:before {
  display: none;
}

.main .buy-crypto .roadmap-img:last-child::after {
  display: none;
}

.main .buy-crypto form {
  width: 74%;
  background-color: #fff;
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 32px;
  transition: all 0.4s linear;
}

.main .buy-crypto form:hover {
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.2);
}

.main .mobile-apps {
  position: relative;
  overflow: hidden;
}

.main .mobile-apps::before {
  content: "";
  position: absolute;
  bottom: -398px;
  left: -433px;
  width: 735px;
  height: 735px;
  background: rgba(252, 227, 77, 0.3);
  filter: blur(300px);
}

.main .mobile-apps::after {
  content: "";
  position: absolute;
  width: 735px;
  height: 735px;
  left: 1105px;
  top: -14px;
  background: rgba(252, 227, 77, 0.3);
  filter: blur(300px);
  animation: blink 3s infinite linear;
}

.main .mobile-apps .phones {
  height: 734px;
  object-fit: cover;
  position: absolute;
  bottom: -260px;
  z-index: 1;
}

.main .news {
  padding: 64px 0;
}

.main .netexKassa {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.main .netexKassa .netexKassaPreview {
  height: 59%;
  position: absolute;
  bottom: 0;
  object-fit: cover;
}

@media (min-width: 1280px) {
  .main .benefits .MuiGrid-grid-lg-3 {
    flex-grow: 0;
    max-width: 18%;
    flex-basis: 18%;
  }
}

@media (max-width: 992px) {
  .main .buy-crypto {
    padding: 32px 0;
  }
  .main .mobile-apps .phones {
    height: 626px;
    position: absolute;
    bottom: -128px;
  }
  .main .buy-crypto .roadmap {
    flex-direction: column;
    height: 367px;
    width: 100%;
    align-items: center;
  }
  .main .buy-crypto .roadmap-img::after {
    top: unset;
    transform: rotate(90deg);
    bottom: -50%;
    left: 0;
  }
  .main .buy-crypto .roadmap-img::before {
    display: none;
  }
  .main .buy-crypto form {
    width: 100%;
    margin-top: 32px;
  }
  /* .main .netexKassa {
    position: relative;
    height: 537px;
  } */
  .main .news {
    padding: 40px 0;
  }
}

@media (max-width: 768px) {
  .main .firstScreen .column {
    flex-direction: column;
  }
  .main .mobile-apps .phones {
    height: 300px;
    width: 100%;
    position: absolute;
    bottom: -50px;
  }
}

@media (max-width: 576px) {
  .main .firstScreen .box {
    width: 100% !important;
  }
  .main .netexKassa {
    position: relative;
    height: 484px;
  }
  .main .netexKassa .netexKassaPreview {
    width: 100%;
    height: 170px;
    bottom: 0;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
